.block-editor-block-list__block.wp-block.wp-block-create-block-tabs {
	border: 1px dotted #bababa;
	background: #fff;
	padding: 10px;
	h2 {
		font-size: 1em;
	}
	.wp-block-create-block-tab {
		display: flex;
		flex-direction: column;
		border: 1px solid #333;
		padding: 5px;
		margin-bottom: 10px;

		.tab-title {
			display: flex;
			flex-direction: row;
			align-items: center; /* add this line */
		}

		.tab-icon {
			margin-right: 5px;
			font-size: 18px;
		}

		.tab-label_input {
			flex-grow: 1;
			margin-right: 10px;
		}
	}

	.block-editor-inner-blocks {
		background-color: #fff;
		margin-top: 10px;
	}
}
.icons {
	display: flex;
	flex-wrap: wrap;
}

.icons input {
	width: 100%;
	margin: 5px 0px;
}

.icons button {
	margin: 5px;
	padding: 2px;
	background-color: transparent;
	border: none;
}

.icon-option.active {
	background-color: #0091bd;
	color: white;
}

.icon-option.hovered {
	background-color: #00a0d2;
	color: white;
}
